.logo {
  @include verticalCenter;
  margin: 15px auto;
  width: 200px;
  border-radius: 20px;
  padding: 15px 0;
  box-shadow: 0 2px 3px rgba(0, 0, 0, 0.307);
  background-color: $color-1;
  color: rgb(58, 58, 58);

  img {
    height: 40px;
  }

  h3 {
    margin-left: 10px;
    color: $color-1;
  }
}